@import "./variables.scss";

body{
    margin: 0;
}

.footer{
    background: $primary;
    display: flex;
    justify-content:center;
    gap: 2rem;
    align-items: center;
    padding: 1rem;

    &__image{
        flex: 1;
        display: flex;
        align-self: center;
        max-width: 35px;
        min-width: 25px;
        height: auto;
    }
}